import React from 'react';
import { Link } from 'react-scroll';

const Navbar = () => (
  <nav className="flex bg-gray-800 p-4 fixed w-full items-baseline justify-between">
    <div className="flex">
      <div className="text-white font-bold text-xl">Logo</div>
    </div>

    <div className="flex">
      <Link to="section1" smooth duration={500} className="text-white mx-2 cursor-pointer">
        Section 1
      </Link>
      <Link to="section2" smooth duration={500} className="text-white mx-2 cursor-pointer">
        Section 2
      </Link>
      <Link to="section3" smooth duration={500} className="text-white mx-2 cursor-pointer">
        Section 3
      </Link>
    </div>
  </nav>
);

export default Navbar;